.wrap {
  display: flex;
  gap: 29px;
  flex-wrap: wrap;
}

@media only screen and (min-width: 768px) {
  .wrap {
    display: flex;
    gap: 24px;
    flex-wrap: wrap;
  }
}

@media only screen and (min-width: 1214px) {
  .wrap {
    display: flex;
    gap: 29px;
    flex-wrap: wrap;
  }
}

.LoadMoreBtn {
  margin: 0 auto;
  margin-top: 100px;

  font-size: 16px;
  font-weight: 500;
  text-decoration-line: underline;
  cursor: pointer;

  color: var(--primary-color-blue);
  background-color: transparent;
}

.LoadMoreBtn:hover {
  color: var(--secondary-color-blue);
}

.textNotFound {
  font-size: 24px;
  font-weight: 500;
  margin: 0 auto;
  color: var(--primary-color-blue);
}
